.your-rights-hero-cont,
.your-rights-img-cont {
    position: absolute;
    top: -45px;
    right: 0;
    transform: translate(0%);
    bottom: -45px;
}


.your-rights-img-top {
    position: absolute;
    z-index: 999;
    background: url(../images/your-rights-hero-1.jpg);
    background-size: cover;
    background-position: center center;
    height: 470px;
    width: 410px;
    border-radius: 5px;
    right: -230px;
    top: 58px;
}

.your-rights-img-bottom {
    position: absolute;
    z-index: 998;
    background: url(../images/your-rights-hero-2.jpg);
    background-size: cover;
    background-position: center center;
    height: 380px;
    width: 380px;
    border-radius: 5px;
    bottom: 155px;
    left: 180px;
}

.your-rights-img-back {
    position: absolute;
    z-index: 997;
    background: url(../images/your-rights-img-back.jpg);
    background-size: cover;
    height: 260px;
    width: 260px;
    border-radius: 5px;
    top: 170px;
    left: 250px;
}

.your-rights-pattern-top-left {
    position: absolute;
    z-index: 997;
    top: 140px;
    left: 170px;
    width: 140px;
}

.your-rights-pattern-right {
    position: absolute;
    z-index: 996;
    left: 510px;
    width: 115px;
    bottom: 40px;
}

.your-rights-pattern-bottom {
    position: absolute;
    z-index: 995;
    bottom: 100px;
    height: 85px;
    left: 140px;
}


@media screen and (max-width: 1200px) {
    .your-rights-hero-cont,
    .your-rights-img-cont {
        position: absolute;
        transform: translate(20%, 0);
        width: 60% !important;
        max-width: 60% !important;
        bottom: -50px;
        top: -40px;
        right: 0px;
        flex: 0 0 60%;
    }

    .your-rights-img-top {
        height: 370px;
        width: 370px;
        border-radius: 5px;
        right: -30px;
        top: 58px;
    }

    .your-rights-img-bottom {
        height: 330px;
        width: 350px;
        border-radius: 5px;
        bottom: 345px;
        left: 70px;
    }

    .your-rights-img-back {
        height: 200px;
        width: 200px;
        border-radius: 5px;
        top: 190px;
        left: 140px;
    }

    .your-rights-pattern-top-left {
        position: absolute;
        z-index: 997;
        top: 30px;
        left: 50px;
        width: 140px;
    }

    .your-rights-pattern-right {
        position: absolute;
        z-index: 996;
        left: 400px;
        width: 115px;
        bottom: 270px;
    }

    .your-rights-pattern-bottom {
        position: absolute;
        z-index: 995;
        bottom: 290px;
        height: 85px;
        left: 10px;
    }
}

@media screen and (max-width: 991px) {
    .your-rights-hero-cont, .your-rights-img-cont {
        position: relative;
        top: 0;
        right: 0;
        width: 100% !important;
        max-width: 100% !important;
        transform: translate(0%);
        bottom: 0;
        height: 450px;
        flex: 0 0 100%;
    }

    .your-rights-img-top {
        height: 370px;
        width: 370px;
        border-radius: 5px;
        right: -30px;
        top: 58px;
    }

    .your-rights-img-bottom {
        height: 290px;
        width: 300px;
        border-radius: 5px;
        bottom: 0;
        left: 40px;
    }

    .your-rights-img-back {
        display: none;
    }

    .your-rights-pattern-right {
        position: absolute;
        z-index: 996;
        left: auto;
        width: 115px;
        bottom: 180px;
        right: -90px;
        top: 20px;
    }

}

@media screen and (max-width: 576px) {
    .your-rights-hero-cont,
    .your-rights-img-cont {
        position: relative;
        top: 0;
        right: 0;
        transform: translate(0%);
        bottom: 0;
        height: 450px;
    }

    .your-rights-img-top {
        height: 270px;
        width: 270px;
        border-radius: 5px;
        right: 30px;
        top: 58px;
    }
}

// Images at the bottom of the page
.your-rights-img-cont {
    .your-rights-img-top {
        background: url(../images/your-rights-img-top.jpg);
        background-position: right;
        background-size: cover;
    }

    .your-rights-img-bottom {
        background: url(../images/your-rights-img-bottom.jpg);
        background-size: cover;
    }

    .your-rights-img-back {
        background: url(../images/your-rights-img-back.jpg);
        background-size: cover;
    }
}
