[data-toggle*=popover] {
  cursor: pointer;
}

.popover {
  color: #000;
  font-size: 0.875rem;
  border-radius: 15px;
  border: none;
  text-align: center;
}
.popover .popover-header {
  border: none;
  border-radius: 15px 15px 0 0;
}
.popover .popover-body p:last-child {
  margin-bottom: 0;
}
.popover-regular {
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.25);
}
.popover-regular .arrow {
  background-image: url("../images/speech-point-small.svg");
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  z-index: 5;
  top: auto;
  bottom: calc(100% - 8px);
  width: 29px;
  height: 25px;
  padding: 3px 3px 0;
}
.popover-regular .arrow:after {
  display: none;
}
.popover-regular .arrow:before {
  content: "";
  position: absolute;
  top: calc(100% - 8px);
  width: 29px;
  height: 10px;
  background-color: #fff;
}
.popover-regular.bs-popover-top .arrow, .popover-regular.bs-popover-auto[x-placement^=top] .arrow {
  top: calc(100% - 8px);
  bottom: auto;
  transform: rotate(180deg) scaleX(-1);
}
.popover-large {
  border: 4px solid #000000;
  border-radius: 20px;
  max-width: 273px;
  text-align: left;
}
.popover-large .popover-body {
  padding: 25px;
  font-size: 14px;
  font-weight: 400;
}
.popover-large .popover-body .bold {
  font-size: 1rem;
  font-weight: 700;
}
.popover-large .arrow-large {
  top: calc(100% - 8px);
  width: 56.6px !important;
  height: 55.7px !important;
  background-image: url("../images/speech-point-large.svg");
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  z-index: 5;
  transform: translate3d(25px, 0, 0);
}
.popover-large .arrow-large:before, .popover-large .arrow-large:after {
  border: none;
}
.popover-large.bs-popover-bottom .arrow-large, .popover-large.bs-popover-auto[x-placement^=bottom] .arrow-large {
  bottom: calc(100% - 8px);
  top: auto;
  left: calc(50% - 40px) !important;
  transform: rotate(180deg) scaleX(-1);
}
.popover-large.bs-popover-left .arrow-large, .popover-large.bs-popover-auto .arrow-large {
  top: 10px !important;
  left: calc(100% - 8px) !important;
  right: auto;
  transform: scaleX(-1) rotate(90deg);
}
.popover-hover {
  pointer-events: none;
}
.popover-doctor {
  margin-top: -150px;
}
@media (min-width: 992px) {
  .popover-doctor {
    max-width: 255px;
  }
}
.popover-doctor ul > li {
  font-size: 1.25rem;
  margin: 0 0 22px 0;
  position: relative;
  padding: 0 0 0 40px;
}
.popover-doctor ul > li:before {
  content: "";
  position: absolute;
  top: 5px;
  left: 0;
  width: 11px;
  height: 11px;
  background-color: #0d69f2;
  border-radius: 50%;
}
.popover-doctor .close {
  position: absolute;
  top: 10px;
  right: 10px;
  cursor: pointer;
  opacity: 1 !important;
}
.popover-cqc {
  min-width: 450px;
}
.popover.popover-max-width {
  width: 200px;
}

.special-offers-slide {
  position: absolute;
  /* Style for "Rounded Re" */
  width: 404px;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16);
  border-radius: 20px;
  background-image: linear-gradient(258deg, #ed0373 0%, #ffffff 100%);
  background-position: right top;
  background-size: 180%;
  top: 0;
  right: 0;
  color: #fff;
  padding: 15px;
  transform: rotate3d(0, 1, 0, 90deg);
  transition: 0.3s all ease;
  transform-origin: right top;
  opacity: 0;
  pointer-events: none;
}
.special-offers-slide.show {
  transform: rotate3d(0, 1, 0, 0deg);
  opacity: 1;
  z-index: 10;
  pointer-events: initial;
}
.special-offers-slide .close-offer {
  position: absolute;
  width: 10px;
  height: 10px;
  right: 10px;
  top: 10px;
  cursor: pointer;
}
.special-offers-slide .close-offer svg {
  width: 10px;
  height: 10px;
}
.special-offers-slide .close-offer svg path {
  fill: #fff;
}
.special-offers-slide .special-offer-title {
  font-size: 1.375rem;
  margin-bottom: 0.4rem;
}
.special-offers-slide ul {
  width: 80%;
  padding-left: 25px;
}
.special-offers-slide ul li {
  list-style: none;
  font-size: 0.75rem;
  position: relative;
  margin-bottom: 12px;
}
.special-offers-slide ul li:before {
  content: url("/images/icons/circle-check.svg");
  width: 20px;
  height: 20px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 0;
}

.test-page .special-offers-slide {
  position: static;
}